import Tower from "./Tower"

cc.Class({
    extends: cc.Component,
    properties: {
        TowerPrefabs:{
			default:[],
			type:Tower
		}
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    onLoad(){
        
    },

    on_RetractAllMenu_Clicked:function(){
        for(let i=0;i<this.TowerPrefabs.length;i++)
            this.TowerPrefabs[i].RetractMenu();
    }

    // update (dt) {},
});
